<template>
  <div>
    <app-head :allProShow="false" :cartNum="cartNum"></app-head>
    <div class="product">
      <div class="container">
        <div class="pro-view">
          <div class="view-list">
            <img :src="selectImg" alt ref="pImg">
          </div>
          <!-- <div class="view-control">
            <div class="control-lr">
              <a href="#" class="control-prev">上一张</a>
              <a href="#" class="control-next">下一张</a>
            </div>
            <div class="control-page"></div>
          </div>-->
        </div>
        <div class="pro-info">
          <h1 class="pro-title">{{proInfo.name}}</h1>
          <p class="pro-desc">{{proInfo.desc}}</p>
          <div class="pro-price">
            <span>
              <label class="price">{{proInfo.price}}</label>元
            </span>
          </div>
          <div class="pro-choose">
            <div class="choose1">
              <p class="choose-title">
                选择
                <span class="prop-name">{{proInfo.prop1_name}}</span>
              </p>
              <ul class="choose-list clearfix">
                <li
                  v-for="(version,index) in proInfo.options"
                  :key="`version_${index}`"
                  :class="{active:selectedVersionIndex===index}"
                  @click="changeVersion(index)"
                >
                  <a href="javascript:;">
                    <span class="list-name">{{version.prop1_value}}</span>
                    <span class="list-price">
                      <label class="price">{{version.price}}</label>元
                    </span>
                  </a>
                </li>
              </ul>
            </div>
            <div class="choose2">
              <p class="choose-title">
                选择
                <span class="prop-name">{{proInfo.prop2_name}}</span>
              </p>
              <ul
                class="choose-list clearfix"
                v-for="(version,idx2) in proInfo.options"
                :key="`v_${idx2}`"
                v-if="idx2 === selectedVersionIndex"
              >
                <li
                  v-for="(color,idx) in version.list"
                  :key="`color_${idx}`"
                  :class="{active:selectedColorIndex===idx}"
                  @click="changeColor(idx)"
                >
                  <a href="javascript:;">
                    <span class="list-name">{{color.prop2_value}}</span>
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div class="pro-btn">
            <a href="javascript:;" id="addCart" @click="addToCart">加入购物车</a>
          </div>
          <el-dialog :visible.sync="dialogVisible" width="30%">
            <span>成功加入购物车！</span>
            <span>点击“确定”按钮前往购物车查看</span>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="goToCart">确 定</el-button>
            </span>
          </el-dialog>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import appHead from "../components/Header";
import api from "../../utils/api";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      proInfo: {},
      selectedVersionIndex: 0,
      selectedColorIndex: 0,
      selectedSkuId: "",
      selectedColor: "",
      dialogVisible: false, //对话框
      cartNum: 0 ,//购物车数量
    };
  },
  components: {
    appHead
  },
  computed: {
    ...mapState("user", ["loggedIn"]),
    selectImg(){
        return this.proInfo.options[this.selectedVersionIndex].list[this.selectedColorIndex].imgs[0];
    }
  },
  created() {
    const id = this.$route.params.id;
    // 商品详细
    api.get("/productDetail?pid=" + id, { _silent: true }).then(data => {
      console.log(data);
      this.proInfo = data;
      this.selectedColor = this.proInfo.options[0].list[0].prop2_value;
      this.selectedSkuId = this.proInfo.options[0].list[0].sku_id;
    });
    if (this.loggedIn) {
      api.get("/user/cart").then(data => {
        this.cartNum = data.length;
      });
    }
  },
  methods: {
    ...mapActions("user", ["checklogin"]),
    addToCart() {

        //   加入购物车
        api.post("/user/addCart", { sku_id: this.selectedSkuId }).then(
          data => {
            this.dialogVisible = true;
            this.cartNum++;
          },
          err => {
            this.$router.push("/login");
          }
        );
    //   });
    },
    goToCart() {
      this.dialogVisible = false;
      this.$router.push("/cart");
    },
    changeVersion(vi) {
      this.selectedVersionIndex = vi;
      this.selectedColorIndex = this.proInfo.options[vi].list.findIndex(
        el => el.prop2_value == this.selectedColor
      );
      if (this.selectedColorIndex < 0) {
        // console.log("没找着", this.selectedColor);

        this.selectedColorIndex = 0;
      }
      this.$refs.pImg.src = this.proInfo.options[vi].list[
        this.selectedColorIndex
      ].imgs[0];
      this.selectedSkuId = this.proInfo.options[vi].list[
        this.selectedColorIndex
      ].sku_id;
    },
    changeColor(ci) {
      this.selectedColorIndex = ci;
      this.$refs.pImg.src = this.proInfo.options[
        this.selectedVersionIndex
      ].list[ci].imgs[0]; //图片
      this.selectedColor = this.proInfo.options[this.selectedVersionIndex].list[
        ci
      ].prop2_value; //颜色
      this.selectedSkuId = this.proInfo.options[this.selectedVersionIndex].list[
        ci
      ].sku_id; //sku_id
    }
  }
};
</script>

<style scoped>
@import url("../css/product.css");
@import url("../css/iconfont.css");
@import url("../css/style.css");
</style>
